@import url('http://fonts.cdnfonts.com/css/bakbak-one');

body {
  font-family: 'Bakbak One', sans-serif !important; 
  font-weight: normal;
  font-size: 16px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
div,
button {
  font-family: 'Poppins', sans-serif !important;
  font-weight: normal;
}

/* sub navbar menu  css*/
.dropbtn {
  position: relative;
  transition: 0.5s;
}

.dropdown {
  position: relative;
  display: inline-block;
  transition: 0.5s;
}

.dropdown-content {
  opacity: 0;
  display: none;
  position: absolute;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  top: 1.4em;
  transition: 0.5s;
}

.dropdown-content a {
  color: black;
  padding: 3px 16px;
  text-decoration: none;
  display: block;
  transition: 0.5s;
}


.dropdown:hover .dropdown-content {
  display: block;
  opacity: 1;
}


/* sub navbar menu  css end*/

/* hero section background image css add */

.herosbg {
  position: relative;
}

.herosbg::before {
  position: absolute;
  content: '';
  background-image: url('../src/assets/h1_banner_bg.png');
  height: 100%;
  width: 100%;
  z-index: 0;
  background-repeat: no-repeat;
  background-size: cover;
  /* opacity: 0.2; */

}
.ming-bg {
  position: relative;
 
  background-image: url('../src/assets/mint-1_bg.png');
  height: 100%;
  width: 100%;
  z-index: 0;
  background-repeat: no-repeat;
  background-size: cover; 
  background-attachment: fixed;

}
.ming-bg::after {
  position: absolute;
  left: 0px;
  top: 0px;
  height: 100%;
  width: 100%;
  content: "";
  background: linear-gradient(rgba(4, 12, 18, 0.8) 0%, rgb(4, 12, 18) 100%);
  z-index: -1;
}

.rotated-style {
  background: #ff9800;
  height: 143px;
  width: 143px;
  position: absolute;
  border-radius: 100%;
  display: flex;
  top: -3rem;
  left: -2.4rem;
}

img.mint_live_text {
  animation: 20s linear 0s infinite normal none running Djvmn;
}

@keyframes Djvmn {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}


.tokensbg {
  height: 400px;
  display: grid;
  align-items: center;
}

.tokensbg::before {
  position: absolute;
  content: '';
  background-image: url('../src/assets/Simulation-Metaverse.png');
  height: 100%;
  width: 100%;
  z-index: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  opacity: 0.1;

}


.animation-logo {
  position: relative;
}

.animation-logo:nth-child(1) {
  top: -6rem;
  animation: wwrrightstar 7s infinite linear;
}

.animation-logo:nth-child(2) {
  top: 6rem;
  animation: wwrrightstar 15s infinite linear;
}

.animation-logo:nth-child(3) {
  top: -3rem;
  animation: wwrrightstar 5s infinite linear;
}

.animation-logo:nth-child(4) {
  top: -7rem;
  animation: wwrrightstar 8s infinite linear;
}

.animation-logo:nth-child(5) {
  top: 7rem;
  left: -3rem;
  animation: wwrrightstar 10s infinite linear;
}

.animation-logo:nth-child(6) {
  animation: wwrrightstar 3.6s infinite linear;
}

.animation-logo:nth-child(7) {
  top: -6rem;
  animation: wwrrightstar 4s infinite linear;
}

.animation-logo:nth-child(8) {
  top: 6rem;
  animation: wwrrightstar 7s infinite linear;
}

.animation-logo:nth-child(9) {
  animation: wwrrightstar 12s infinite linear;
}


@keyframes wwrrightstar {
  0% {
    transform: translateY(-20%);
  }

  50% {
    transform: translateY(20%);
  }

  100% {
    transform: translateY(-20%);
  }
}


.boxfirst,
.boxbalance {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.boxfirst .secarea::before {
  position: absolute;
  content: '';
  background-color: #151516;
  width: 100px;
  height: 100px;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  border-radius: 50%;
  transition: 0.5s;
  transform: translate3d(-50px, 20rem, 10px);
}

.boxfirst:hover .secarea::before {
  position: absolute;
  content: '';
  background-color: #ff9800;
  background: linear-gradient(49deg, transparent, #ff9800);
  width: 310px;
  height: 310px;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  transform: translate3d(-27px, -8rem, 10px);
}


.boxbalance .balancearea::before {
  position: absolute;
  content: '';
  background-color: #383737;
  width: 100px;
  height: 100px;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  border-radius: 50%;
  transition: 0.5s;
  transform: translate3d(-50px, -3rem, 10px);
}

.boxbalance:hover .balancearea::before {
  position: absolute;
  content: '';
  background-color: #ff9800;
  background: linear-gradient(49deg, transparent, #ff9800);
  width: 310px;
  height: 310px;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  transform: translate3d(-16px, -9rem, 10px);
}

.eccosarea::before {
  position: absolute;
  content: '';
  background-image: url('../src/assets/image_processing20210903-11142-rgcjlz.gif');
  height: 100%;
  width: 100%;
  z-index: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
