.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    /* display: block; */
    /* width: 100%;
    height: 100%;
    object-fit: cover; */
}

.swiper-button-next {
    width: 40px;
    height: 40px;
    border-radius: 50px;
    background: #ff9800;
    color: #fff;
    right: 0; 
}
 
.swiper-button-prev {
    width: 40px;
    height: 40px;
    border-radius: 50px;
    background: #ff9800;
    color: #fff;
    left: 0; 
}

.swiper-button-prev:after,
.swiper-rtl .swiper-button-next:after,
.swiper-button-next:after,
.swiper-rtl .swiper-button-prev:after {
    font-size: 25px;
}